<script>
    import ChangeOS from "./ChangeOS.svelte";
    let optNames = ["win", "mac", "linux"];

    let currOS = localStorage.getItem("chosenOS");
    let plat = window.navigator.platform.toLowerCase();
    let changeOS;

    if (!currOS) {
        optNames.forEach((os) => {
            if (plat.includes(os)) {
                currOS = os;
            }
        });
    }

    // change this to just make an automatic assumption based off of what OS the user actually has - much easier.
    // if this doesnt exist then give an option to choose an OS
</script>

<ChangeOS bind:this={changeOS} bind:currOS />
<div class="OS-specific-page">
    {#if !currOS}
        <div class="selection">
            <div class="group">
                <h2>Select an OS</h2>
                <div class="options">
                    {#each ["Windows", "Mac", "Linux"] as group}
                        <button class="button" on:click={changeOS.changeOS()}>
                            {group}
                        </button>
                    {/each}
                </div>
            </div>
        </div>
    {/if}
    <div class={currOS}>
        {#if currOS === optNames[0] || currOS === "windows"}
            <slot name="windows" />
        {:else if currOS === optNames[1]}
            <slot name="mac" />
        {:else if currOS === optNames[2]}
            <slot name="linux" />
        {/if}
    </div>
</div>
<br />
