    let currOS = localStorage.getItem("chosenOS");
    let plat = window.navigator.platform.toLowerCase();

    if (!currOS) {
        optNames.forEach((os) => {
            if (plat.includes(os)) {
                currOS = os;
            }
        });
    }

    let macStr = `<kbd>⌘Command</kbd> + <kbd>Alt</kbd> + <kbd>I</kbd>`;
    let otherStr = `<kbd>Ctrl</kbd> + <kbd>Shift</kbd> + <kbd>I</kbd>`;
</script>

