<script>
    import Navbar from "../Navbar.svelte";
    import VisualIns from "./subpages/visualins.svelte";
    import VisualPTIns from "./subpages/visualptins.svelte";
    import VisualUnIns from "./subpages/visualunins.svelte";
    import FAQ from "./subpages/faq.svelte";
    import Support from "./subpages/support.svelte";
    import Install from "./subpages/install.svelte";
    import Crash from "./subpages/crash.svelte";
    import Manual from "./subpages/manual.svelte";
    import Links from "./subpages/links.svelte";
    import Credit from "./subpages/credit.svelte";
    import Submit from "./subpages/submit.svelte";
    import Vanilla from "./subpages/vanilla.svelte";

    import Main from "./betterdiscord.svelte";

    const components = {
        "visual-ins": VisualIns,
        "visual-unins": VisualUnIns,
        "visual-ptins": VisualPTIns,
        faq: FAQ,
        support: Support,
        install: Install,
        crash: Crash,
        manual: Manual,
        links: Links,
        credit: Credit,
        submit: Submit,
        vanilla: Vanilla,
        home: Main,
    };

    let currentPage;

    const showPage = (hash) => {
        let page;
        if (hash) {
            page = hash.slice(1);
        }
        currentPage = page;
    };

    const anchorClicked = () => {
        // console.log(window.location);
        showPage(window.location.hash);
    };

    window.addEventListener("hashchange", anchorClicked);
    showPage(window.location.hash);

    $: TagName = components[currentPage || "home"];
</script>

<Navbar />
<svelte:component this={TagName} />
