    <Details summary="Submission Process"
        >Submitting themes and plugins is done on the{" "}
        <a href="https://betterdiscord.app">BetterDiscord website</a>. Steps to
        submit:
        <br />
        1. Go to the BetterDiscord website, and go to the Themes page to submit themes,
        or the Plugins page to submit plugins.
        <br />
        2. Connect your Discord account to the website from the top right, by clicking
        "Connect" button as is shown here:
        <br />
        <br />
        <img src="https://i.imgur.com/HAJTNlF.png" alt="Connect" />
        <br />
        <br />
        3. Click on the <code>Submit a theme</code> or{" "}
        <code>Submit a plugin</code> on the respective page.
        <br />
        <br />
        <img src="https://i.imgur.com/G7A2I4j.png" alt="Submit a theme" />
        <br />
        <br />
        4. Add your Github account on the modal that pops up.
        <br />
        5. Once the Github account has been connected, please wait at least 2 days
        for the repository to be accepted. If you do not follow the guidelines listed
        below, you will NOT have your add-on accepted.
        <br />
        6. If your account is accepted, you will then be able to use the same submission
        button in step 3 to submit the add-on you wish to. This will then be verified
        by the BetterDiscord team.</Details>
    <Details summary="Submission Guidelines">
        <h4>General</h4>
        <ul>
            <li>All addons must be in public GitHub repositories.</li>
            <li>All addons must not negatively affect users.</li>
            <li>All addons must not target specific users or groups.</li>
            <li>
                All addons must not collect user data without opt-in consent.
            </li>
        </ul>
        <h4>Themes</h4>
        <ul>
            <li>
                Themes must only use @import with Google Fonts or GitHub. This
                does allow for GitHub-related CDNs as well as GitHub pages.
            </li>
            <li>
                Themes must not abuse GitHub's raw links for @import statements.
            </li>
        </ul>
        <h4>Plugins</h4>
        <ul>
            <li>
                Plugins must not introduce new remote libraries. Existing remote
                libraries can still be used, but are officially deprecated to be
                disallowed in the future.
            </li>
            <li>Plugins must not be obfuscated and/or deceitful.</li>
            <li>Plugins must not modify globals or existing prototypes.</li>
            <li>Plugins must not use BD globals outside of BdApi.</li>
            <li>
                Plugins must not attempt to modify the BD UI. This is partially
                for consistency and partially to decrease the number of render
                issues caused by plugins.
            </li>
            <li>
                Plugins and their corresponding libraries shall not operate
                outside of their intended functionality. This includes but is
                not limited to swapping out components unrelated to the plugin,
                introducing additional buttons, badges or other unrelated
                elements.
            </li>
            <li>
                Plugins must not make use of or read user tokens, emails, or
                passwords.
            </li>
        </ul>
        <p>
            Source from{" "}
            <a
                href="https://github.com/BetterDiscord/BetterDiscord/wiki/Addon-Policies">
                BetterDiscord's Addons Policies page
            </a>
        </p>
    </Details>

