<script>
    import Modal from "./Modal.svelte";
    export let currOS;
    export const changeOS = (e) => {
        currOS = e.target.innerText.toLowerCase();
        localStorage.setItem("chosenOS", currOS);
    };

    let modal;
    let isOpenModal = false;

    const showModal = () => {
        setTimeout(() => {
            isOpenModal = true;
        }, 0);
    };
</script>

<Modal bind:this={modal} bind:isOpenModal>
    <svelte:fragment slot="content">
        <div class="modal-content">
            <h2>Select an OS</h2>
            <div class="options">
                {#each ["Windows", "Mac", "Linux"] as group}
                    <button
                        class="button"
                        on:click={(e) => {
                            changeOS(e);
                            modal.closeModal();
                        }}>
                        {group}
                    </button>
                {/each}
            </div>
        </div>
    </svelte:fragment>
</Modal>
{#if currOS}
    <div class="changeOS">
        Currently showing {currOS.charAt(0).toUpperCase() +
            currOS.slice(1)}{" "}<button class="button" on:click={showModal}
            >Change OS Shown</button>
    </div>
{/if}
