<script>
    import Details from "../components/Details.svelte";
    import Header from "../components/Header.svelte";
    import HomeButton from "../components/HomeButton.svelte";
    import ChoicesPages from "../components/ChoicesPages.svelte";
    import OSPages from "../components/OSPages.svelte";
    import changeActive from "../../scripts/ChangeActive";

    let optNames = [
        "Installation Guide",
        "Installation Errors",
        "Installation Issues",
    ];
</script>

<svelte:head>
    <title>BetterDiscord Guides - Install</title>
    <meta content="BetterDiscord Guides - Install" property="og:title" />
    <meta
        content="How to use the Installer and how to fix issues with the Installer."
        property="og:description" />
</svelte:head>
<section id="install">
    <Header header="BetterDiscord Support - Installer" />
    <p>
        Guide for using the Installer for BetterDiscord, and its common issues.
    </p>
    <ChoicesPages>
        <svelte:fragment slot="choices">
            {#each optNames as name}
                <li>
                    <button class="options" on:click={changeActive}
                        >{name}</button>
                </li>
            {/each}
        </svelte:fragment>
        <svelte:fragment slot="output">
            <div class="choice">
                <p>
                    You can follow the guide <a href="#visual-ins">here</a> or
                    follow the instructions below:
                    <br />
                    <br />
                    Here is a guide on how to use the installer.
                    <br />
                    1. Download the installer from the{" "}
                    <a
                        href="https://betterdiscord.app"
                        target="_blank"
                        rel="noopener noreferrer">
                        official website
                    </a>{" "}
                    (and delete any old installers that you have)
                    <br />
                    2. Open the installer. If it does not open, go to{" "}
                    <a href="#install-issue">this link</a>.
                    <br />
                    3. Click that you accept the license agreement, then "next".
                    If you cannot do so, click specifically on the text next to the
                    checkbox, as is shown below.
                    <br />
                    <br />
                    <img
                        src="https://i.imgur.com/aIjzoAf.png"
                        alt="Click the text!" />
                    <br />
                    4. Click "install", then "next".
                    <br />
                    5. Click on the version that you wish to install BetterDiscord
                    onto, then click "install". It should show a path to the "resources"
                    folder for that install, but if it doesn't, go to
                    <a href="#install-issue">this link</a>.
                    <br />
                    6. It should install and say that is has successfully installed.
                    If there is an error message, go to{" "}
                    <a href="#install-error">this link</a> and look for your
                    specifc error.
                    <br />
                    7. BetterDiscord should now be installed. You can check this
                    by going to the settings part of Discord (
                    <img
                        src="https://i.imgur.com/H1XCRcO.png"
                        alt="Cog icon"
                        class="cog-img" />
                    ), with the new settings as shown below. If it is not, please
                    follow
                    <a href="#install-issue">this link</a>.
                    <br />
                    <br />
                    <img
                        src="https://i.imgur.com/siYiZRQ.png"
                        alt="BetterDiscord Settings" />
                </p>
            </div>
            <div class="choice">
                <p>
                    These are specifically for error which occur while
                    installing BetterDiscord. If you have an issue like the
                    installer not opening, go to{" "}
                    <a href="#install-issue">this link</a>
                    <br />
                    If you are unsure of how to use the installer, or get stuck,
                    go to
                    <a href="#install-guide">this link</a>
                </p>
                <Details
                    summary="Unable to click the checkbox to proceed through to the
										installation"
                    >Click the text right next to the checkbox, as shown below:
                    <img
                        src="https://i.imgur.com/aIjzoAf.png"
                        alt="Click the text!" /></Details>
                <Details
                    summary="No installation folder location found on the install page"
                    >Please go to <a href="#install-issue">this link</a> and go
                    to the part which says "No install path OR BetterDiscord not
                    installed after using installer" for your specific Operating
                    System.
                    <br />
                    (Note: there is not a location listed for Linux, rather, use
                    the link listed on the page.)</Details>
                <Details
                    summary={'"&#10060; Cannot read property "assets" of undefined" OR installer freezing at "downloading asar file..."'}
                    >You have an old installer that is not getting the asar file
                    from the new GitHub repository. Follow these steps:
                    <br />
                    1. Remove all of the installers that you have currently
                    <br />
                    2. Get a new installer from{" "}
                    <a
                        href="https://betterdiscord.app"
                        target="_blank"
                        rel="noopener noreferrer">
                        the BetterDiscord website
                    </a></Details>
                <Details
                    summary={'"&#10060; EACCES: permission denied, mkdir", or any error where at "shims" there is a "mkdir" error'}
                    >Your install of Discord is corrupted, and you will need to
                    redownload Discord. Help can be found{" "}
                    <a
                        href="https://discordtips.com/how-to-fully-uninstall-discord/"
                        target="_blank"
                        rel="noopener noreferrer">
                        here
                    </a></Details>
                <Details
                    summary={'"&#10060; Cannot read property "hasOwnProperty" of undefined" error'}
                    >There are quite a few things that could be causing this
                    issue.
                    <br />
                    1. Get a new installer from{" "}
                    <a
                        href="https://betterdiscord.app"
                        target="_blank"
                        rel="noopener noreferrer">
                        the BetterDiscord website
                    </a>
                    . Delete any that you have before this.
                    <br />
                    2. Either change your location on a VPN you are using to the
                    US if you are outside of America (mainly China or other places
                    where activity is limited), or disable your VPN, Proxy, AntiVirus
                    or Firewall.
                    <br />
                    3. Try again with the Discord application closed.
                    <br />
                    4. Fully reinstall Discord. Help can be found{" "}
                    <a
                        href="https://discordtips.com/how-to-fully-uninstall-discord/"
                        target="_blank"
                        rel="noopener noreferrer">
                        here
                    </a></Details>
                <Details
                    summary={'"&#10060; getaddrinfo ENOTFOUND api.github.com"'}
                    >This usually caused by your internet service provider
                    interfering with the DNS lookup.
                    <br />
                    1. Disable your AntiVirus/VPN and run the installer again
                    <br />
                    <br />
                    2. Find your operating system on{" "}
                    <a
                        href="https://www.ionos.com/digitalguide/server/configuration/how-to-change-dns-server/"
                        target="_blank"
                        rel="noopener noreferrer">
                        this guide
                    </a>
                    <br />
                    Follow the steps to change the DNS server addresses to either
                    of these:
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;a. Google - server 1: 8.8.8.8 server
                    2: 8.8.4.4
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;b. Cloudflare - server 1 1.1.1.1 server
                    2: 1.0.0.1
                    <br />
                    Then try the installer again.
                    <br />
                    <br />
                    3.{" "}
                    <a
                        href="https://www.ionos.com/digitalguide/server/configuration/flush-dns/"
                        target="_blank"
                        rel="noopener noreferrer">
                        Flush your DNS cache.
                    </a></Details>
                <Details summary="Any other error while installing"
                    >Try disabling any VPN, Proxy, AntiVirus and Firewall that
                    you have, and try again. These can also occur from a slow
                    internet connection.</Details>
            </div>
            <div class="choice">
                <OSPages>
                    <svelte:fragment slot="windows">
                        <Details
                            summary="No install path OR BetterDiscord not installed after using installer">
                            If you do not have a path to the Discord folder to
                            install BetterDiscord into, or BetterDiscord did not
                            show after installing, then press "browse" and paste
                            <code>
                                %localappdata%/discord/app-1.0.9005/resources
                            </code>
                            into the top bar.
                            <br />
                            If you do not know how to do that, look to the video
                            below.
                            <br />
                            If BetterDiscord is still not on your Discord after installing,
                            then use the path
                            <code>
                                %programdata%/%username%/Discord/app-1.0.9005/resources
                            </code>
                            .
                            <br />
                            If neither of these work, reinstall Discord and try again.
                            <br />
                            This is shown below:
                            <br />
                            <br />
                            <img
                                src="https://cdn.discordapp.com/attachments/542030029861814284/863124888974000198/programdata.gif"
                                alt="BetterDiscord Settings" />
                        </Details>
                        <Details summary="Installer does not open">
                            If the installer seems to not want to open at all,
                            then follow these instructions:
                            <br />
                            1. Download and install{" "}
                            <a
                                href="https://7-zip.org"
                                target="_blank"
                                rel="noopener noreferrer">
                                7-zip</a
                            >{" "}
                            (if you do not have it already)
                            <br />
                            2. Create a new folder (can be anywhere)
                            <br />
                            3. Right click on the BetterDiscord.exe file, and from
                            the 7zip options, choose 'extract to...' and choose the
                            folder you just created
                            <br />
                            4. Use the exe file in the folder you extracted to.
                            <br />
                            <br />
                            If this does not work, then try the{" "}
                            <a
                                href="https://github.com/BetterDiscord/Installer/releases/download/v1.0.0-hotfix/BetterDiscord-Windows.exe"
                                target="_blank"
                                rel="noopener noreferrer">
                                hotfix</a
                            >.
                            <br />
                            <br />
                            If none of these solutions solved your problem, then
                            try{" "}
                            <a href="#manual">
                                manually installing BetterDiscord
                            </a>
                        </Details>
                        <Details
                            summary="Installer shows black instead of opening"
                            >If you get a black screen, try the following:{" "}
                            <br />
                            1.Disable your AntiVirus.
                            <br />
                            2. Press <kbd>⊞Win</kbd> + <kbd>R</kbd> and paste
                            <code>ipconfig /flushdns</code>
                            <br />
                            3. If that doesn't work, try to restart your computer,
                            or try the hotfix linked above.</Details>
                    </svelte:fragment>
                    <svelte:fragment slot="mac">
                        <Details
                            summary="No install path OR BetterDiscord not installed after using installer"
                            >If you do not have a path to the Discord folder to
                            install BetterDiscord into, then press "browse" and
                            then <kbd>⌘Command</kbd>
                            + <kbd>Shift</kbd> +
                            <kbd>G</kbd>, then paste
                            <code>
                                /Applications/Discord/Contents/Resources
                            </code>
                            into the popup that appears.</Details>
                        <Details summary="Installer does not open"
                            >For the popup of "Developer cannot be verified",
                            you can either:
                            <br />
                            Click on the <kbd>?</kbd> icon at the top right of
                            the popup
                            <br />
                            Go to system preferences, go to the security and privacy
                            tab, then select "general" and click the padlock &#128274;
                            at the bottom right, and log in. Then in the "Allow apps
                            downloaded from:" section, select "anywhere."{" "}
                            <a
                                href="https://it.nmu.edu/docs/allowing-third-party-applications-install-macbook">
                                Link to a guide here.
                            </a>
                            <br />
                            <br />
                            If the issue listed above was not your problem, then
                            try{" "}
                            <a href="#manual-mac">
                                manually installing BetterDiscord
                            </a>
                            .</Details>
                    </svelte:fragment>
                    <svelte:fragment slot="linux">
                        <p>
                            If you are having issues with the regular installer,
                            then try using{" "}
                            <a
                                href="https://gist.github.com/ObserverOfTime/d7e60eb9aa7fe837545c8cb77cf31172"
                                target="_blank"
                                rel="noopener noreferrer">
                                betterdiscordctl
                            </a>{" "}
                            instead of the regular BD installer. If that does not
                            work, you can instead try{" "}
                            <a href="#manual-nix">
                                manually installing BetterDiscord
                            </a>
                            .
                        </p>
                    </svelte:fragment>
                </OSPages>
            </div></svelte:fragment>
    </ChoicesPages>

    <HomeButton />
</section>
