        property="og:description" />
</svelte:head>
<section id="vanilla">
    <Header header="Vanilla Commands" />
    <p>
        SmolAlli's guide to using the vanilla command to get access to regular
        Discord temporarily.
        <br />
        To restore: Close down Discord completely, then re-open it.
    </p>
    <OSPages>
        <svelte:fragment slot="windows"
            ><h3>
                How to use the <code>--vanilla</code> command:
            </h3>
            <p>
                1. Close Discord
                <br />
                2. Press <kbd>⊞Win</kbd> + <kbd>R</kbd>
                <br />
                3. Paste{" "}
                <code>
                    %localappdata%\Discord\app-1.0.9005\discord.exe --vanilla
                </code>{" "}
                into the chatbox that appears
            </p>
            <br />
            <h3>Vanilla Discord Desktop shortcut:</h3>
            <p>
                1. On the desktop, right click, choose "New" and then "Create
                Shortcut"
                <br />
                2. You can either create a shortcut that stops Discord then re-opens
                it (a), or just opens it (b).
                <br />
                &nbsp;&nbsp;&nbsp;&nbsp;a. Paste into the filepath{" "}
                <code>
                    powershell.exe Stop-Process -Name Discord; start
                    %USERPROFILE%\AppData\Local\Discord\app-1.0.9005\Discord.exe
                    --vanilla
                </code>
                <br />
                &nbsp;&nbsp;&nbsp;&nbsp;b. Paste into the filepath{" "}
                <code>
                    powershell.exe start
                    %USERPROFILE%\AppData\Local\Discord\app-1.0.9005\Discord.exe
                    --vanilla
                </code>
                <br />
                3. Choose a name for the shortcut, and voila! A shortcut.
                <br />
                4. (optional): You can get the icon that Discord uses from
                <code>%USERPROFILE%\AppData\Local\Discord\app.ico</code> so that
                the shortcut looks good.
            </p></svelte:fragment>
        <svelte:fragment slot="mac"
            ><h3>
                How to use the <code>--vanilla</code> command:
            </h3>
            <p>
                1. Close Discord
                <br />
                2. Open terminal with <kbd>⌘Command</kbd> +<kbd>Spacebar</kbd>
                <br />
                3. Paste in{" "}
                <code>open /Applications/Discord.app --args --vanilla</code>
            </p></svelte:fragment>
        <svelte:fragment slot="linux"
            ><h3>
                How to use the <code>--vanilla</code> command:
            </h3>
            <p>
                1. Close Discord
                <br />
                2. Open up a terminal instance
                <br />
                3. Paste in <code>discord --vanilla</code>
            </p></svelte:fragment>
