<script>
    import Header from "../components/Header.svelte";
    import HomeButton from "../components/HomeButton.svelte";
    import OSPages from "../components/OSPages.svelte";
    import changeActive from "../../scripts/ChangeActive";
    import Video from "../components/Video.svelte";

    function copyText(e) {
        let elem = e.target.previousElementSibling;
        elem.select();
        elem.setSelectionRange(0, 99999);

        // Copy the text inside the text field
        navigator.clipboard.writeText(elem.value);
    }
</script>

<svelte:head>
    <title>BetterDiscord Guides - Manual</title>
    <meta content="BetterDiscord Guides - Manual" property="og:title" />
    <meta
        content="Step by step instructions for manually installing BetterDiscord."
        property="og:description" />
</svelte:head>
<section id="manual">
    <Header header="Manually Installing BetterDiscord" />
    <p>
        Please follow this guide CAREFULLY to install BetterDiscord, if the
        regular installer will not work! Any OS will work for this, but please
        go to your specific OS.
    </p>
    <Video
        video="https://user-images.githubusercontent.com/90428263/162848023-9816afe8-8e77-44b5-afe2-054fd9131700.mp4" />

    <OSPages>
        <svelte:fragment slot="windows">
            <p>
                1. Install{" "}
                <a
                    href="https://github.com/git-for-windows/git/releases/download/v2.34.1.windows.1/Git-2.34.1-64-bit.exe"
                    >Git
                </a>{" "}
                and{" "}
                <a href="https://nodejs.org/dist/v16.13.1/node-v16.13.1-x64.msi"
                    >Node.js
                </a>{" "}
                (Required)
                <br />
                2. Open up Windows Powershell (NOT Administrator) and check that
                you are NOT in the system32 directory. Change that with the follow
                command:
                <code>cd ~/</code>
                <br />
                3. Copy/paste the following commands:
                <br />
                <br />
                This below copies all of the commands into one mega command to simplify
                the process.
            </p>
            <div class="choices sub">
                <div class="choices-group">
                    <ul class="sub">
                        <li>
                            <button
                                class="options active"
                                on:click={changeActive}>
                                Powershell
                            </button>
                        </li>
                        <li>
                            <button class="options" on:click={changeActive}>
                                Command Prompt
                            </button>
                        </li>
                    </ul>
                </div>
                <div class="output-group">
                    <div class="choice active">
                        <input
                            type="text"
                            value="git clone https://github.com/BetterDiscord/BetterDiscord.git; cd ./BetterDiscord/; npm install; npm run build; npm run inject"
                            readonly
                            id="copy-1" />
                        <button on:click={copyText} class="button">
                            Click here to copy
                        </button>
                    </div>
                    <div class="choice">
                        <input
                            type="text"
                            value="git clone https://github.com/BetterDiscord/BetterDiscord.git && cd ./BetterDiscord/ && npm i && npm run build && npm run inject"
                            readonly
                            id="copy-2" />
                        <button on:click={copyText} class="button">
                            Click here to copy
                        </button>
                    </div>
                </div>
            </div>
            <p>
                <br />
                <br />
                <code>
                    git clone https://github.com/BetterDiscord/BetterDiscord.git
                </code>
                <br />
                <br /> <code>cd ./BetterDiscord/</code>
                <br />
                <br /> <code>npm install</code>
                <br />
                <br /> <code>npm run build</code>
                <br />
                <br /> <code>npm run inject</code>
                <br />
                <br />
                For that last command, if you are using a non-stable install of Discord,
                add <code>canary</code> or <code>ptb</code> at the end of the command.
            </p>
        </svelte:fragment>
        <svelte:fragment slot="mac"
            ><p>
                1. Install <a href="https://git-scm.com/download/mac">Git</a>
                and{" "}
                <a href="https://nodejs.org/dist/v16.13.1/node-v16.13.1.pkg"
                    >Node.js</a
                >{" "}
                (Required)
                <br />
                2. Open up MacOS Terminal (using <kbd>⌘Command</kbd> +
                <kbd>spacebar</kbd> and type in "Terminal")
                <br />
                3. Copy/paste the following commands:
                <br />
                <br />
                This below copies all of the commands into one mega command to simplify
                the process.
                <input
                    type="text"
                    value="git clone https://github.com/BetterDiscord/BetterDiscord.git && cd ./BetterDiscord/ && npm i && npm run build && npm run inject"
                    readonly
                    id="copy" />
                <button on:click={copyText} class="button">
                    Click here to copy
                </button>
                <br />
                <br />
                <code>
                    git clone https://github.com/BetterDiscord/BetterDiscord.git
                </code>
                <br />
                <br /> <code>cd ./BetterDiscord/</code>
                <br />
                <br /> <code>npm install</code>
                <br />
                <br /> <code>npm run build</code>
                <br />
                <br /> <code>npm run inject</code>
                <br />
                <br />
                For that last command, if you are using a non-stable install of Discord,
                add <code>canary</code> or <code>ptb</code> at the end of the command.
            </p></svelte:fragment>
        <svelte:fragment slot="linux"
            ><p>
                1. Install <a href="https://git-scm.com/download/linux">Git</a
                >{" "}
                and{" "}
                <a href="https://nodejs.org/dist/v16.13.1/node-v16.13.1.tar.gz"
                    >Node.js</a
                >{" "}
                (Required)
                <br />
                2. Open up Terminal
                <br />
                Copy/paste the following commands:
                <br />
                <br />
                This below copies all of the commands into one mega command to simplify
                the process.
                <input
                    type="text"
                    value="git clone https://github.com/BetterDiscord/BetterDiscord.git && cd ./BetterDiscord/ && npm i && npm run build && npm run inject"
                    readonly
                    id="copy" />
                <button on:click={copyText} class="button">
                    Click here to copy
                </button>
                <br />
                <br />
                <code>
                    git clone https://github.com/BetterDiscord/BetterDiscord.git
                </code>
                <br />
                <br /> <code>cd ./BetterDiscord/</code>
                <br />
                <br /> <code>npm install</code>
                <br />
                <br /> <code>npm run build</code>
                <br />
                <br /> <code>npm run inject</code>
                <br />
                <br />
                For that last command, if you are using a non-stable install of Discord,
                add <code>canary</code> or <code>ptb</code> at the end of the command.
            </p></svelte:fragment>
    </OSPages>

    <h2>Resources/Credit</h2>
    <p>
        Source from{" "}
        <a
            href="https://github.com/BetterDiscord/BetterDiscord#manual-installation">
            BetterDiscord
        </a>
    </p>
    <br />
    <HomeButton />
</section>
